// Rubik
@font-face {
	font-family: "Rubik";
	src: url("/public/fonts/Rubik/Rubik-Regular.woff2") format("woff2");
	font-weight: normal;
	font-style: normal;
}

@font-face {
	font-family: "Rubik";
	src: url("/public/fonts/Rubik/Rubik-Bold.woff2") format("woff2");
	font-weight: bold;
	font-style: normal;
}

@font-face {
	font-family: "Rubik";
	src: url("/public/fonts/Rubik/Rubik-Italic.woff2") format("woff2");
	font-weight: normal;
	font-style: italic;
}

@font-face {
	font-family: "Rubik";
	src: url("/public/fonts/Rubik/Rubik-BoldItalic.woff2") format("woff2");
	font-weight: bold;
	font-style: italic;
}

